.imgContainer {
    height: 100%;
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    /* -moz-background-size: cover; */
    /* -webkit-background-size: cover; */
    /* -o-background-size: cover; */
    background-position: 50%;
  }
  
  .imgContainer .loginImage {
    position: absolute;
    height: 100%;
    width: 100%;
    /* inset: 0px; */
    object-fit: cover;
    object-position: center top;
    color: transparent;
    opacity: 0.7;
  }
  
  .loginContainer {
    position: relative;
    height: 100%;
    overflow: auto;
  }
  
  .loginContainer .loginPadding {
    margin-top: 60px;
    display: flex;
    background-repeat: no-repeat;
    min-height: 720px;
    background-position: 50%;
    background-size: cover;
    position: relative;
  }
  
  .loginItem {
    width: 100%;
    padding: 32px 64px;
    justify-content: flex-start;
    align-items: center;
    gap: 64px;
    flex: 1 1;
    text-align: center;
    display: flex;
  }
  
  .loginContent {
    width: 50%;
    text-align: left;
    height: 100%;
    padding-bottom: 64px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 32px;
    display: inline-flex;
  }
  
  .loginTitle {
    align-self: stretch;
    color: hsla(0, 0%, 100%, 0.96);
    font-size: 64px;
    font-family: var(--lexen_deca), sans-serif;
    font-weight: 700;
    overflow-wrap: break-word;
  }
  
  .loginItem .loginFormContent {
    overflow-x: hidden;
    padding: 0;
    display: flex;
    /* -webkit-backdrop-filter: blur(4px); */
    backdrop-filter: blur(4px);
  }
  
  .loginFormPadding {
    width: 100%;
    height: 100%;
    padding: 48px;
    background: rgba(26, 29, 36, 0.72);
    border-radius: 16px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 48px;
    display: inline-flex;
  }
  
  .loginHeading {
    align-self: stretch;
    text-align: center;
    color: hsla(0, 0%, 100%, 0.96);
    font-size: 32px;
    font-family: var(--lexen_deca), sans-serif;
    font-weight: 200;
    overflow-wrap: break-word;
  }
  
  .loginTabs{
    align-self: stretch;
    height: 264px;
    padding-left: 36px;
    padding-right: 36px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
    display: flex;
    text-align: left;
  }

  .loginTabs label{
    font-size: 17px;  
  }

  .loginTabs input{
    width: 100%;
  }
  
  .btn1 {
    font-weight: 600;
    font-size: 16px;
    align-self: stretch;
    padding: 12px 16px;
    background: #f4f4f7;
    border-radius: 96px;
    justify-content: center;
    align-items: center;
    display: inline-flex;
    color: rgba(26, 29, 36, 0.96);
    border-color: #fff;
    min-width: 50%;
    cursor: pointer;
  }
  
  .loginMethods {
    flex: 1 1;
    height: 24px;
    padding-left: 12px;
    padding-right: 12px;
    justify-content: center;
    align-items: center;
    gap: 4px;
    display: flex;
    word-wrap: break-word;
  }
  
  .loginLogo {
    width: 24px;
    height: 24px;
  }
  
  .forgetMenu {
    align-self: stretch;
    padding: 12px 16px;
    /* background: #222429; */
    border-radius: 96px;
    justify-content: center;
    align-items: center;
    display: inline-flex;
  }
  
  .forgetContent {
    flex: 1 1;
    height: 24px;
    padding-left: 12px;
    padding-right: 12px;
    justify-content: center;
    align-items: center;
    gap: 4px;
    display: flex;
  }
  
  .ForgetButton {
    flex: 1 1;
    text-align: center;
    color: #6299eb;
    font-size: 16px;
    font-family: var(--lexen_deca), sans-serif;
    font-weight: 600;
    overflow-wrap: break-word;
    cursor: pointer;
  }
  
  
  .loginPolicy{
      align-self: stretch;
      text-align: center;
      font-size: 16px;
      color: hsla(0,0%,100%,.96);
      font-weight: 300;
      letter-spacing: .03px;
      font-family: var(--lexen_deca),sans-serif;
  }
  
  a{
      text-decoration: none;
      cursor: pointer;
      color: #6299eb;
      margin: 5px;
      font-size: 13px;
  }